@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import './vars.scss'; 

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}

html {
  scroll-behavior: smooth;
}


body {
  font-family: 'Poppins', sans-serif;
  background: $color-bg;
  color: $color-white;
  line-height: 2;
}

.container { 
  width: $container-width-lg;
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 2rem;
}

section > h2, section > h5 {
  text-align: center;
  color: $color-light;
}

section > h2 {
  color: $color-primary;
  margin-bottom: 3rem;
}

.text-light {
  color: $color-light;
}

a {
  color: $color-primary;
  transition: $transition;
}

a:hover {
  color: $color-white; 
}

.btn {
  width: max-content;
  display: inline-block;
  color: $color-primary;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid $color-primary;
  transform: $transition;
}

.btn:hover {
  background: $color-white;
  color: $color-bg;
  border-color: transparent;
}

.btn-primary {
  background-color: $color-primary;
  color: $color-bg;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* Media Queries */
@media screen and (max-width: 1024px) {
  .container {
      width: $container-width-md;
  } 

  section > h2 {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
      width: $container-width-sm;
  } 

  section > h2 {
    margin-top: 2rem;
  }
}